import React, { useState, useEffect } from 'react';
import './App.css';
import './index.css'
import Form from './components/Form'
import Filter from './components/Filter'
import FilterAndDisplay from './components/FilterAndDisplay'
import personService from './services/persons'

/* this phonebook app contains a form which allows the user to search, add, delete, and update their contact list.
   This is handled using controlled components*/
const App = () => {
  // event handlers and states stay in the main App function
  const [persons, setPersons] = useState([])
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [filter, setNewFilter] = useState('')
  const [errorMessage, setErrorMessage] = useState('')
  const [successMessage, setSuccessMessage] = useState('')

  useEffect(() => {
    personService
      .getAll()
      .then(initialPersons => {
        console.log('promise fulfilled')
        setPersons(initialPersons)
      })
  }, [])

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }
  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }
  const handleFilterChange = (event) => {
    setNewFilter(event.target.value)
  }

  const Notification = ({ error_message, success_message }) => {
    if (errorMessage)
      return <div className="error"> {errorMessage} </div>
    else if (successMessage)
      return <div className="success"> {successMessage} </div>
    return null
  }


  const deleteInfo = (id, name) => {
    let deleteIf = window.confirm(`Delete ${name}?`)
    if (deleteIf) {
      personService
        .deleteEntry(id)
        .then(response => {
          window.location.reload()
          setSuccessMessage(`${name} was deleted`)
          setTimeout(() => {
            setSuccessMessage(null)
          }, 5000)
        })
        .catch(error => {
          setErrorMessage(`the person ${name} was already deleted from server`)
          setTimeout(() => {
            setErrorMessage(null)
          }, 5000)
        })
    }
  }

  const updateInfo = (id, name) => {
    const personObject = {
      name: newName,
      number: newNumber,
    }
    personService
      .update(id, personObject)
      .then(response => {
        setNewName('')
        setNewNumber('')
        window.location.reload();
        setSuccessMessage(`${name} was updated`)
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
      })
      .catch(error => {
        setErrorMessage(`${name} cannot be updated because they have already been deleted from the phonebook`)
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
      })
  }


  const addInfo = (event) => {
    event.preventDefault()

    // Check if name is already in the phonebook 
    let existingId;
    const same = persons.filter(function (persons) {
      if (persons.name === newName)
        existingId = persons.id;
      return persons.name === newName;
    })

    if (same.length > 0) {
      let updateIf = window.confirm(`${newName} is already added to phonebook, replace the old number with new one?`)
      if (updateIf) {
        updateInfo(existingId, newName)
      }
    }
    else {
      const personObject = {
        name: newName,
        number: newNumber,
      }
      personService
        .create(personObject)
        .then(returnedPerson => {
          setPersons(persons.concat(returnedPerson))
          setNewName('')
          setNewNumber('')
          window.location.reload()
          setSuccessMessage(`${newName} was added`)
          setTimeout(() => {
            setSuccessMessage(null)
          }, 5000)
        })
        .catch((error) => {
          setErrorMessage(`${newName} could not be added. Please try again.`)
          setTimeout(() => {
            setErrorMessage(null)
          }, 5000)
        })
    }
  }

  const FilterAndDisplay = () => {
    let filteredList = []
    if (filter === '')
      filteredList = persons
    else {
      filteredList = persons.filter(function (person) { return person.name.toLowerCase().includes(filter.toLowerCase()) })
    }
    return filteredList.map(function (person) {
      return <div key={person.id}> {person.name} {person.number}
        <button onClick={() => deleteInfo(person.id, person.name)}> Delete</button></div>
    })
  }

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification errorMessage={errorMessage} successMessage={successMessage} />
      <div>
        <Filter filter={filter} handleFilterChange={handleFilterChange} />
      </div>
      <h2> Add a new: </h2>
      <Form addInfo={addInfo} newName={newName} handleNameChange={handleNameChange}
        newNumber={newNumber} handleNumberChange={handleNumberChange} />
      <h2>Numbers</h2>
      <div> {<FilterAndDisplay persons={persons} filter={filter} />} </div>
    </div>
  )
}

export default App;
